"use client";
import Image from "next/image";
import React, { useState } from "react";
import homeImg from "@/public/assets/imgs/Home Page.jpg";
import Input from "@/app/components/ui/Input";
import { MdOutlineLocationOn } from "react-icons/md";
import { COLORS } from "@/app/constants";

const Page = () => {
  const [activeTab, setActiveTab] = useState<"details" | "drinks">("details");

  // بيانات وهمية للمشروبات (يمكن استبدالها ببيانات حقيقية)
  const drinks = [
    { name: "فانيليا سوت ذوقي", price: "47.50", count: 4 },
    { name: "كابتشينو كلاسيك", price: "35.00", count: 2 },
    { name: "موكا مثلج", price: "52.00", count: 1 },
  ];

  return (
    <div className="px-4 py-6 text-right bg-gray-50 min-h-screen">
      {/* صورة المقهى */}
      <div className="flex justify-center mb-4">
        <Image 
          src={homeImg} 
          alt="كافهينو" 
          width={500} 
          className="rounded-3xl shadow-md" 
        />
      </div>

      {/* عنوان المقهى */}
      <div className="mb-6">
        <h1 className="text-2xl font-bold text-center mb-2" style={{ color: COLORS.BRAND_PRIMARY }}>
          كافهينو
        </h1>
        <div className="flex items-center justify-center gap-1 text-gray-600">
          <MdOutlineLocationOn size={18} />
          <span>المعادي - شارع النصر</span>
        </div>
      </div>

      {/* التبويبات */}
      <div className="flex gap-4 mt-6 border-b">
        <div
          onClick={() => setActiveTab("details")}
          className={`cursor-pointer px-4 py-3 transition w-full text-center
            ${activeTab === "details" 
              ? "font-bold border-b-2" 
              : "text-gray-500"}`}
          style={activeTab === "details" ? { 
            color: COLORS.BRAND_PRIMARY, 
            borderColor: COLORS.BRAND_PRIMARY 
          } : {}}
        >
          تفاصيل الكافيه
        </div>

        <div
          onClick={() => setActiveTab("drinks")}
          className={`cursor-pointer px-4 py-3 transition w-full text-center
            ${activeTab === "drinks" 
              ? "font-bold border-b-2" 
              : "text-gray-500"}`}
          style={activeTab === "drinks" ? { 
            color: COLORS.BRAND_PRIMARY, 
            borderColor: COLORS.BRAND_PRIMARY 
          } : {}}
        >
          المشروبات
        </div>
      </div>

      {/* محتوى التبويب النشط */}
      <div className="mt-6">
        {activeTab === "details" && (
          <div className="p-5 bg-white rounded-2xl shadow-sm">
            <h2 className="font-bold mb-4 text-lg">تفاصيل الكافيه</h2>
            <p className="text-gray-700 leading-7 mb-4">
              كافهينو يمزج بين الدفء والراحة، مصمم بلمسة من الطابع التقليدي واللمسات المصرية. 
              يتميز بمساحات مناسبة للعمل والدراسة مع موسيقى هادئة تزيد من استرخاء الزوار.
            </p>
            <div className="space-y-2 text-gray-600">
              <p><span className="font-medium">مواعيد العمل:</span> 9 صباحًا - 12 منتصف الليل</p>
              <p><span className="font-medium">العنوان:</span> المعادي - شارع النصر</p>
            </div>
          </div>
        )}

        {activeTab === "drinks" && (
          <div className="bg-white rounded-2xl shadow-sm overflow-hidden">
            <h2 className="font-bold p-5 text-lg border-b">المشروبات</h2>
            
            {drinks.map((drink, index) => (
              <div key={index}>
                {Array.from({ length: drink.count }).map((_, i) => (
                  <div key={i} className="p-4 border-b flex justify-between items-center">
                    <div>
                      <h3 className="font-medium">{drink.name}</h3>
                    </div>
                    <div className="text-left">
                      <span className="font-bold">{drink.price} جنيه</span>
                    </div>
                  </div>
                ))}
              </div>
            ))}
            
            <div className="p-4">
              <button 
                className="w-full py-3 rounded-xl text-white font-bold text-center"
                style={{ backgroundColor: COLORS.BRAND_PRIMARY }}
              >
                الطلب الآن
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Page;